.venue-page {
  max-height: 92vh;
  height: 92vh;
  min-height: 100%;
  width: 100%;
}
.venue-content {
  min-height: 100%;
  min-width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.venue-pane {
  min-height: 100%;
  width: 50%;
  flex-grow: 1;
  position: relative;
}
.venue-pane.images {
  display: flex;
  flex-direction: column;
  min-height: 92vh;
}

.venue-image-pane {
  flex-grow: 1;
  min-width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.venue-image-pane.image {
  flex-basis: 50%;
}
.venue-image-pane.image.cincinnati-club {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
}
.venue-image-pane.map {
  min-height: 50%;
  max-height: 100%;
  flex-grow: 1;
  background-color: #888;
}
.venue-image {
  width: 100%;
}
.event-date {
  margin-top: 1em;
}

.venue-detail {
  margin-top: 1em;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}
.venue-description {
  margin: 20px;
  margin-bottom: 30px;
  font-size: 1.2em;
}
.description-paragraph:not(:last-child) {
  margin-bottom: 1em;
}
.venue-title {
  font-size: 1.5em;
  margin-bottom: 15px;
}
.event-site-address {
  text-align: center;
  max-width: 500px;
  cursor: pointer;
}

.event-site-address .info-line-content {
  margin: 0 0.7em;
}
@media (max-width: 800px) {
  .venue-content {
    flex-direction: column;
  }
  .venue-image-pane.image.st-louis {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
  }
  /* .venue-image{
    width: auto;
    height: 100%;;
  } */
  .venue-pane {
    width: auto;
  }
}
